.list{
    color: seashell;
    width: 300px;
    background-color: rgb(150, 10, 138);
    height: 50px;
    margin: 20px auto;
    border-radius: 5px;


}
.list p{
    padding: 10px;
    position: relative;
    transition: ease-in-out;

}
.list:hover{
    background-color: rgb(170, 43, 170);
}
.list p span{
    position: absolute;
    right: 10px;
    
}
.faicons{
    margin: 0 5px;
    font-size: 15px;
    cursor: pointer;
}
.list p input{
    background-color: transparent;
    border :0;
    color: rgb(255, 230, 247);
    font-size: 18px;
    
}
.list p input:focus{
    outline:none;

}
.itemsAnim-enter{
    opacity: 0.01;
}
.itemsAnim-enter .itemsAnim-enter-active{
    opacity: 1;
    transition: opacity 300ms ease-in
}
.itemsAnim-leave{
    opacity: 1;
}
.itemsAnim-leave .itemsAnim-leave-active{
    opacity: 0.01;
    transition: opacity 300ms ease-in
}


