body{
  background-color:#9266ff ;
  
}
.App{
  background-color: #3f3160;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
  border-radius: 12px;
}
h1{
  color: rgb(219, 191, 197);
  font-style: inherit;
  font-family: serif;
  padding-left: 100px;
  padding-top: 15px;
  
}
#to-do-form input{
  background-color: rgb(27, 112, 137);
  border:0;
  width:200px;
  height:50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 10px;
  color: rgb(234, 238, 237);



}

#to-do-form input::placeholder{
  color:rgba(240, 236, 236, 0.5);
  font-size: 16px;
  
  }
  #to-do-form input:hover{
    background-color: rgb(30, 163, 150);
  
  }

#to-do-form input:focus{
  outline: none;

}
#to-do-form button:focus{
  outline: none;

}
#to-do-form button{
  height: 47px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color: rgb(139, 214, 202);
  font-size: 14px;
  cursor: pointer;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
  color: rgba(11, 48, 29, 0.5);
  font-size: 15px;
}

#to-do-form button:hover {
  background-color: rgb(149, 228, 228);
}



